// src/pages/Home.js
import React, { useState, useEffect } from 'react';
import TaskCard from '../components/TaskCard';
import { useSwipeable } from 'react-swipeable';

const Home = () => {
  const [tasks, setTasks] = useState([]);
  const [completedTasks, setCompletedTasks] = useState([]);
  const [newTask, setNewTask] = useState({
    label: '',
    points: '',
    icon: '',
    category: 'Sonstiges',
    repeat: false,
    isGoalTask: false,
    goal: '',
  });

  useEffect(() => {
    const storedTasks = JSON.parse(localStorage.getItem('tasks')) || [];
    const storedCompleted = JSON.parse(localStorage.getItem('completed')) || [];
    setTasks(storedTasks);
    setCompletedTasks(storedCompleted);
  }, []);

  useEffect(() => {
    localStorage.setItem('tasks', JSON.stringify(tasks));
    localStorage.setItem('completed', JSON.stringify(completedTasks));
  }, [tasks, completedTasks]);

  const handleAddTask = () => {
    if (!newTask.label || !newTask.points || isNaN(newTask.points)) return;

    const task = {
      id: Date.now(),
      label: newTask.label,
      points: parseFloat(newTask.points),
      icon: newTask.icon || '✅',
      category: newTask.category,
      repeat: newTask.repeat,
      isGoalTask: newTask.isGoalTask,
      goal: newTask.isGoalTask ? parseInt(newTask.goal || 1) : undefined,
      progress: 0,
    };

    setTasks([...tasks, task]);
    setNewTask({ label: '', points: '', icon: '', category: 'Sonstiges', repeat: false, isGoalTask: false, goal: '' });
  };

  const handleProgress = (taskId, delta = 1) => {
    setTasks(prevTasks => prevTasks.map(task => {
      if (task.id === taskId) {
        if (task.isGoalTask) {
          const newProgress = Math.max(0, Math.min(task.goal, (task.progress || 0) + delta));
          const isComplete = newProgress >= task.goal;
          if (isComplete && !completedTasks.includes(taskId)) {
            setCompletedTasks([...completedTasks, taskId]);
          }
          return { ...task, progress: newProgress };
        } else {
          if (!completedTasks.includes(taskId)) {
            setCompletedTasks([...completedTasks, taskId]);
          }
          return task;
        }
      }
      return task;
    }));
  };

  const handleUndo = (taskId) => {
    setCompletedTasks(completedTasks.filter(id => id !== taskId));
  };

  const totalPoints = tasks.reduce((sum, task) => {
    if (!completedTasks.includes(task.id)) return sum;
    if (task.isGoalTask && task.goal) {
      return sum + ((task.points * (task.progress || 0)) / task.goal);
    }
    return sum + task.points;
  }, 0);

  const filteredTasks = (done) => tasks.filter(task => done === completedTasks.includes(task.id));

  const SwipeableTask = ({ task, completed }) => {
    const handlers = useSwipeable({
      onSwipedLeft: () => handleProgress(task.id, -1),
      onSwipedRight: () => handleProgress(task.id, +1),
      preventDefaultTouchmoveEvent: true,
      trackMouse: true
    });

    return (
      <div {...handlers}>
        <TaskCard task={task} completed={completed} onProgress={handleProgress} onUndo={handleUndo} />
      </div>
    );
  };

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">Neue Aufgabe erstellen</h1>
      <div className="bg-white p-4 rounded-lg shadow mb-6">
        <div className="flex flex-col gap-2">
          <input type="text" placeholder="Name" value={newTask.label} onChange={e => setNewTask({ ...newTask, label: e.target.value })} className="border p-2 rounded" />
          <input type="number" placeholder="Punkte (€)" value={newTask.points} onChange={e => setNewTask({ ...newTask, points: e.target.value })} className="border p-2 rounded" />
          <input type="text" placeholder="Icon (z. B. 💪)" value={newTask.icon} onChange={e => setNewTask({ ...newTask, icon: e.target.value })} className="border p-2 rounded" maxLength={2} />
          <select value={newTask.category} onChange={e => setNewTask({ ...newTask, category: e.target.value })} className="border p-2 rounded">
            <option>Sport</option>
            <option>Lernen</option>
            <option>Haushalt</option>
            <option>Sonstiges</option>
          </select>
          <label className="flex items-center gap-2">
            <input type="checkbox" checked={newTask.repeat} onChange={e => setNewTask({ ...newTask, repeat: e.target.checked })} /> Täglich wiederholen
          </label>
          <label className="flex items-center gap-2">
            <input type="checkbox" checked={newTask.isGoalTask} onChange={e => setNewTask({ ...newTask, isGoalTask: e.target.checked })} /> Zielaufgabe (mit Zielwert)
          </label>
          {newTask.isGoalTask && (
            <input type="number" placeholder="Zielwert (z. B. 30)" value={newTask.goal} onChange={e => setNewTask({ ...newTask, goal: e.target.value })} className="border p-2 rounded" />
          )}
          <button onClick={handleAddTask} className="bg-green-600 hover:bg-green-700 text-white py-2 rounded mt-2">Aufgabe hinzufügen</button>
        </div>
      </div>

      <h2 className="text-xl font-bold mb-4">Zu erledigen</h2>
      {filteredTasks(false).map(task => (
        <SwipeableTask key={task.id} task={task} completed={false} />
      ))}

      <h2 className="text-xl font-bold mt-8 mb-4">Erledigt</h2>
      {filteredTasks(true).map(task => (
        <SwipeableTask key={task.id} task={task} completed={true} />
      ))}

      <div className="mt-6 text-lg font-semibold text-green-700">Gesamtpunkte: {totalPoints.toFixed(2)} €</div>
    </div>
  );
};

export default Home;
