import React from 'react';
import { BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from 'recharts';

const data = [
  { name: 'Montag', Punkte: 5 },
  { name: 'Dienstag', Punkte: 8 },
  { name: 'Mittwoch', Punkte: 6 },
  { name: 'Donnerstag', Punkte: 10 },
  { name: 'Freitag', Punkte: 7 },
  { name: 'Samstag', Punkte: 4 },
  { name: 'Sonntag', Punkte: 9 },
];

const Charts = () => {
  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">Punkteverlauf der Woche</h1>
      <ResponsiveContainer width="100%" height={300}>
        <BarChart data={data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          <Bar dataKey="Punkte" fill="#3b82f6" />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
};

export default Charts;
