import React, { useState } from 'react';

const Budget = () => {
  const [budget, setBudget] = useState(50);
  const [purchased, setPurchased] = useState([]);
  const [rewards, setRewards] = useState([
    { id: 1, label: 'Neues T-Shirt', price: 20, icon: '👕' },
    { id: 2, label: 'Kinobesuch', price: 12, icon: '🎬' },
    { id: 3, label: 'Kaffee & Kuchen', price: 5, icon: '☕️' },
  ]);

  const [newReward, setNewReward] = useState({ label: '', price: '', icon: '' });
  const [errorMessage, setErrorMessage] = useState('');

  const handleBuy = (reward) => {
    if (purchased.includes(reward.id)) return;
    if (budget < reward.price) {
      setErrorMessage(`❗️ Du hast nicht genug Budget für "${reward.label}".`);
      return;
    }

    setBudget(budget - reward.price);
    setPurchased([...purchased, reward.id]);
    setErrorMessage('');
  };

  const handleAddReward = () => {
    if (!newReward.label || !newReward.price || isNaN(newReward.price)) return;

    const newItem = {
      id: Date.now(),
      label: newReward.label,
      price: parseFloat(newReward.price),
      icon: newReward.icon || '🎁',
    };

    setRewards([...rewards, newItem]);
    setNewReward({ label: '', price: '', icon: '' });
  };

  const openRewards = rewards.filter(r => !purchased.includes(r.id));
  const purchasedRewards = rewards.filter(r => purchased.includes(r.id));

  return (
    <div className="p-4">
      <h1 className="text-xl font-bold mb-4">Belohnungskatalog</h1>
      <div className="mb-4 font-semibold">Verfügbares Budget: {budget.toFixed(2)} €</div>

      {errorMessage && (
        <div className="mb-4 p-3 bg-red-100 text-red-700 rounded">
          {errorMessage}
        </div>
      )}

      {/* Neue Belohnung hinzufügen */}
      <div className="bg-white p-4 rounded-lg shadow-md mb-6">
        <h2 className="text-md font-semibold mb-2">Neue Belohnung hinzufügen</h2>
        <div className="flex flex-col gap-2">
          <input
            type="text"
            placeholder="Name der Belohnung"
            value={newReward.label}
            onChange={(e) => setNewReward({ ...newReward, label: e.target.value })}
            className="border p-2 rounded"
          />
          <input
            type="number"
            placeholder="Preis (€)"
            value={newReward.price}
            onChange={(e) => setNewReward({ ...newReward, price: e.target.value })}
            className="border p-2 rounded"
          />
          <input
            type="text"
            placeholder="Icon (z. B. 🎁)"
            value={newReward.icon}
            onChange={(e) => setNewReward({ ...newReward, icon: e.target.value })}
            className="border p-2 rounded"
            maxLength={2}
          />
          <button
            onClick={handleAddReward}
            className="bg-green-600 hover:bg-green-700 text-white py-2 rounded mt-2"
          >
            Hinzufügen
          </button>
        </div>
      </div>

      {/* Noch offen */}
      <h2 className="text-lg font-bold mb-2">Noch offen</h2>
      {openRewards.length === 0 && <p className="text-gray-500 mb-4">Keine offenen Belohnungen.</p>}
      <ul className="mb-6">
        {openRewards.map((reward) => (
          <li
            key={reward.id}
            className="flex justify-between items-center p-3 mb-3 rounded-lg shadow-sm bg-white"
          >
            <span>
              {reward.icon} {reward.label} – {reward.price} €
            </span>
            <button
              disabled={budget < reward.price}
              onClick={() => handleBuy(reward)}
              className={`px-3 py-1 rounded text-white ${
                budget < reward.price
                  ? 'bg-gray-400 cursor-not-allowed'
                  : 'bg-blue-600 hover:bg-blue-700'
              }`}
            >
              Kaufen
            </button>
          </li>
        ))}
      </ul>

      {/* Bereits gekauft */}
      <h2 className="text-lg font-bold mb-2">Bereits gekauft</h2>
      {purchasedRewards.length === 0 && <p className="text-gray-500">Noch nichts gekauft.</p>}
      <ul>
        {purchasedRewards.map((reward) => (
          <li
            key={reward.id}
            className="flex justify-between items-center p-3 mb-3 rounded-lg shadow-sm bg-green-100"
          >
            <span>
              {reward.icon} {reward.label} – {reward.price} €
            </span>
            <span className="text-sm text-green-700 font-semibold">✅ Gekauft</span>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Budget;
