import React from 'react';

const BottomTabs = ({ activeTab, setActiveTab }) => {
  const tabs = [
    { id: 'home', label: 'Home' },
    { id: 'charts', label: 'Charts' },
    { id: 'budget', label: 'Budget' }
  ];

  return (
    <div className="flex justify-around bg-white shadow-md p-4 border-t">
      {tabs.map((tab) => (
        <button
          key={tab.id}
          onClick={() => setActiveTab(tab.id)}
          className={`\${activeTab === tab.id ? 'text-blue-600 font-bold' : 'text-gray-500'}`}
        >
          {tab.label}
        </button>
      ))}
    </div>
  );
};

export default BottomTabs;
