// src/components/TaskCard.js
import React from "react";

const TaskCard = ({ task, onProgressChange }) => {
  const percentage = task.isGoalTask
    ? Math.min((task.progress / task.goal) * 100, 100)
    : 100;

  const handleClick = (e) => {
    e.preventDefault();
    if (e.type === "click") {
      onProgressChange(task.id, +1);
    } else if (e.type === "contextmenu") {
      onProgressChange(task.id, -1);
    }
  };

  return (
    <div
      onClick={handleClick}
      onContextMenu={handleClick}
      className="relative cursor-pointer border p-4 rounded-xl mb-2 bg-white shadow-md hover:shadow-lg"
    >
      <div
        className="absolute inset-0 rounded-xl"
        style={{
          width: `${percentage}%`,
          backgroundColor: "rgba(126, 255, 196, 0.3)",
          zIndex: 0,
        }}
      />
      <div className="relative z-10 flex justify-between items-center">
        <div className="text-lg">
          {task.icon} {task.label}
        </div>
        <div className="text-sm text-gray-600">
          {task.isGoalTask
            ? `${task.progress}/${task.goal}`
            : `${task.points.toFixed(2)} €`}
        </div>
      </div>
    </div>
  );
};

export default TaskCard;
